"use client";

import type { RefObject } from 'react';
import { Download } from 'lucide-react';
import { toPng } from 'html-to-image';
import { themes, SlideContent, AppLanguage } from '@/components/layouts';
import { PostVisuals } from './post-visuals';

type PostTemplateProps = SlideContent & {
  image: string | null;
  reference: RefObject<HTMLDivElement>;
  language?: AppLanguage;
};

export function PostTemplate(props: PostTemplateProps) {
  const {
    reference,
    seriesTag,
    color,
    phoneStyle = 'default',
    phoneRotation,
    phoneScale = 1,
    phonePosition = { x: 0, y: 0 },
    imagePosition = { x: 50, y: 50 },
    imageZoom = 1,
    imageRotation = 0,
    language = AppLanguage.tr,
  } = props;

  const currentTheme = themes[color] || themes.kidsaiOfficial;

  // Telefon Hareketi (Base deÄŸerler)
  const phoneConfig: Record<string, { y: number, scale: number, rotate: number }> = {
    default: { y: -64, scale: 0.6, rotate: 0 },
    floating: { y: -40, scale: 0.65, rotate: 0 },
    'tilted-left': { y: -32, scale: 0.6, rotate: -3 },
    'tilted-right': { y: -32, scale: 0.6, rotate: 3 },
    flat: { y: -64, scale: 0.7, rotate: 0 },
  };

  const activeConfig = phoneConfig[phoneStyle] || phoneConfig.default;
  
  // Asimetrik temada telefonun pozisyonunu override et
  const themeXOffset = currentTheme.layoutType === 'asymmetric' ? 200 : 0;
  const themeYOffset = currentTheme.layoutType === 'asymmetric' ? 200 : 0;

  const phoneTransformStyle: React.CSSProperties = {
    transform: `translate(${activeConfig.y + phonePosition.x + themeXOffset}px, ${activeConfig.y + phonePosition.y + themeYOffset}px) rotate(${activeConfig.rotate + (phoneRotation || 0)}deg) scale(${activeConfig.scale * phoneScale})`,
  };

  const phoneContentStyle: React.CSSProperties = {
    objectPosition: `${imagePosition.x}% ${imagePosition.y}%`,
    transform: `scale(${imageZoom}) rotate(${imageRotation}deg)`
  };

  const handleDownload = async () => {
    if (reference.current) {
      try {
        const dataUrl = await toPng(reference.current, {
          pixelRatio: 2,
          filter: (node: any) => !node.classList?.contains('exclude-from-export'),
        });
        const link = document.createElement('a');
        link.download = `kidsai-post-${language}-${seriesTag || 'share'}.png`;
        link.href = dataUrl;
        link.click();
      } catch (err) {
        console.error('Hata:', err);
      }
    }
  };

  return (
    <>
        <PostVisuals 
            {...props}
            currentTheme={currentTheme}
            language={language}
            phoneTransformStyle={phoneTransformStyle}
            phoneContentStyle={phoneContentStyle}
        />
        <div className="absolute top-4 right-4 z-50">
            <button
            onClick={handleDownload}
            className="exclude-from-export bg-[#FF4081] text-white p-3 rounded-xl shadow-lg border-2 border-white hover:scale-110 transition-transform"
            title="Kaydet"
            >
            <Download size={24} strokeWidth={3} />
            </button>
        </div>
    </>
  );
}