"use client";

import { Image as ImageIcon, Cloud, Star, Sparkles, ArrowDown, Heart, MessageCircle, Play, ThumbsUp } from 'lucide-react';
import { cn } from '@/lib/utils';
import { ThemeStyle, AppLanguage, LANGUAGE_CONFIG } from '@/components/layouts';

type PostVisualsProps = {
  title: string;
  subtitle: string;
  seriesTag?: string;
  ctaText?: string;
  storeTitle?: string;
  storeSubtitle?: string;
  currentTheme: ThemeStyle;
  language: AppLanguage;
  reference: React.RefObject<HTMLDivElement>;
  image: string | null;
  phoneTransformStyle: React.CSSProperties;
  phoneContentStyle: React.CSSProperties;
  imageFit: 'cover' | 'contain' | 'fill';
};

export function PostVisuals({
  title,
  subtitle,
  seriesTag,
  ctaText,
  storeTitle,
  storeSubtitle,
  currentTheme,
  language,
  reference,
  image,
  phoneTransformStyle,
  phoneContentStyle,
  imageFit
}: PostVisualsProps) {
  
  const langConfig = LANGUAGE_CONFIG[language];
  const isRTL = langConfig.dir === 'rtl';
  const isPop = currentTheme.layoutType === 'asymmetric';

  const titleFontClass = langConfig.fontFallback ? 'font-black tracking-tighter' : 'font-luckiest';
  const bodyFontClass = langConfig.fontFallback ? 'font-bold' : 'font-fredoka font-black';

  return (
    <div ref={reference} className="w-[1080px] h-[1350px] flex-shrink-0 relative overflow-hidden group selection:bg-none font-sans bg-white" dir={langConfig.dir}>
      
      {/* 1. ARKA PLAN */}
      <div className={cn("absolute inset-0 flex flex-col", currentTheme.layoutType === 'centered' ? "items-center" : "", "bg-gradient-to-br", currentTheme.bg, currentTheme.gradient)}>
        
        {/* Arka Planda Devasa Marka YazÄ±sÄ± (Doku) */}
        <div className="absolute inset-0 overflow-hidden opacity-5 pointer-events-none select-none">
            <span className={cn("text-[40rem] text-black leading-none absolute -top-20 -left-20 rotate-[-10deg]", titleFontClass)}>KIDS</span>
            <span className={cn("text-[40rem] text-black leading-none absolute top-[400px] -right-40 rotate-[-10deg]", titleFontClass)}>AI</span>
        </div>

        {/* Desen */}
        <div 
            className={cn("absolute inset-0 pointer-events-none mix-blend-overlay", currentTheme.patternOpacity)}
            style={{
                backgroundImage: isPop 
                  ? 'linear-gradient(#000 1px, transparent 1px), linear-gradient(90deg, #000 1px, transparent 1px)' 
                  : 'radial-gradient(rgba(255,255,255,0.8) 3px, transparent 3px)',
                backgroundSize: '40px 40px'
            }}
        ></div>

        {/* Parlama Efekti (Sadece Centered/Soft) */}
        {currentTheme.glowColor && (
           <div className="absolute inset-0 opacity-60 mix-blend-soft-light pointer-events-none overflow-hidden">
              <div className={cn("absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[2000px] h-[2000px] bg-gradient-radial to-transparent opacity-70", currentTheme.glowColor)}></div>
           </div>
        )}

        {/* --- Ä°Ã‡ERÄ°K YAPISI --- */}
        
        {isPop ? (
          // === ASÄ°METRÄ°K POP-ART DÃœZENÄ° ===
          <div className="relative z-20 w-full h-full p-12 flex flex-col justify-between">
             
             {/* ÃœST KISIM */}
             <div className="flex flex-col items-start gap-6 mt-12">
                {seriesTag && (
                    <div className={cn("bg-black text-white px-6 py-2 rounded-lg transform -rotate-2 border-2 border-white shadow-[4px_4px_0px_rgba(0,0,0,0.2)] text-2xl tracking-widest", titleFontClass)}>
                        {seriesTag}
                    </div>
                )}
                <div className="relative bg-white border-[6px] border-black p-10 rounded-[40px] rounded-bl-none shadow-[10px_10px_0px_rgba(0,0,0,1)] max-w-[800px] transform rotate-1">
                    <div className={cn("inline-block px-4 py-1 rounded-full mb-4 text-xl tracking-widest text-white bg-blue-500 border-2 border-black", bodyFontClass)}>
                        {subtitle}
                    </div>
                    <h1 className={cn("text-[7rem] leading-[0.9] text-black drop-shadow-none whitespace-pre-line", titleFontClass)}>
                        {title}
                    </h1>
                    {/* Balon KuyruÄŸu */}
                    <div className="absolute -bottom-[28px] left-[40px] w-0 h-0 border-l-[30px] border-l-transparent border-r-[30px] border-r-transparent border-t-[30px] border-t-black"></div>
                    <div className="absolute -bottom-[20px] left-[44px] w-0 h-0 border-l-[22px] border-l-transparent border-r-[22px] border-r-transparent border-t-[22px] border-t-white"></div>
                </div>
             </div>

             {/* ORTA KISIM: Dekorasyon */}
             <div className="absolute top-[400px] right-[50px] transform rotate-12">
                <Star size={120} className="text-yellow-400 fill-yellow-400 stroke-black stroke-[3px] drop-shadow-[4px_4px_0px_rgba(0,0,0,1)]" />
             </div>

             {/* ALT KISIM */}
             <div className="flex justify-between items-end w-full relative h-[600px]">
                <div className="flex flex-col gap-4 mb-8 relative z-30">
                    <div className="bg-white border-[4px] border-black p-6 rounded-2xl shadow-[6px_6px_0px_rgba(0,0,0,1)] max-w-[350px]">
                        <h3 className={cn("text-3xl text-black mb-2", bodyFontClass)}>{storeTitle || "Ä°ndir"}</h3>
                        <p className="text-slate-600 font-medium leading-tight mb-4">{storeSubtitle || "ve Dene"}</p>
                        <div className="flex flex-col gap-3">
                            <div className="flex items-center gap-3 bg-black text-white px-4 py-3 rounded-xl border-2 border-transparent"><div className="text-2xl pb-1">ï£¿</div><span className={cn("text-lg", titleFontClass)}>App Store</span></div>
                            <div className="flex items-center gap-3 bg-white text-black border-2 border-black px-4 py-3 rounded-xl"><Play size={20} className="fill-black" /><span className={cn("text-lg", titleFontClass)}>Google Play</span></div>
                        </div>
                    </div>
                    {ctaText && <div className={cn("flex items-center gap-2 text-black bg-yellow-400 px-4 py-2 border-2 border-black rounded-lg shadow-[4px_4px_0px_rgba(0,0,0,1)] w-max transform rotate-[-2deg]", titleFontClass)}>{ctaText}<ArrowDown size={20} /></div>}
                </div>

                <div className="absolute -right-20 -bottom-20 z-20 pointer-events-none">
                    <div className={cn("relative w-[500px] h-[900px] rounded-[64px] overflow-hidden border-[12px] bg-white shadow-[-20px_-20px_0px_rgba(0,0,0,0.15)]", currentTheme.borderColor)} style={phoneTransformStyle}>
                        <div className="w-full h-full bg-white overflow-hidden relative rounded-[50px] border-[4px] border-black">
                            {image ? <img src={image} className="w-full h-full" style={{ objectFit: imageFit, ...phoneContentStyle }} /> : <div className="w-full h-full flex flex-col items-center justify-center bg-yellow-50 p-6 text-center"><Star size={60} className="text-yellow-400 fill-yellow-400 stroke-black stroke-[3px]" /></div>}
                        </div>
                    </div>
                </div>
             </div>
          </div>
        ) : (
          
          // === ORTALANMIÅž HERO DÃœZENÄ° ===
          <div className="relative z-20 w-full h-full flex flex-col items-center pt-20 pb-12 px-8">
             
             {seriesTag && (
                <div className={cn("absolute top-16 z-50 drop-shadow-xl", isRTL ? 'right-12 rotate-[15deg]' : 'left-12 rotate-[-15deg]')}>
                    <div className={cn("border-[4px] border-white shadow-[0_6px_0px_rgba(0,0,0,0.1)] px-5 py-2 rounded-full text-4xl tracking-wider", currentTheme.accent === 'text-white' ? "bg-[#FF4081] text-white" : "bg-gradient-to-br from-yellow-400 to-orange-500 text-white", titleFontClass)}>{seriesTag}</div>
                </div>
             )}

             <div className="flex flex-col items-center gap-5 text-center relative z-30 mt-4">
                <div className={cn("px-8 py-3 rounded-full transform hover:scale-105 transition-transform", currentTheme.pill)}>
                   <p className={cn("text-2xl tracking-widest uppercase drop-shadow-md", bodyFontClass)}>{subtitle}</p>
                </div>
                <div className="relative">
                    <h1 className={cn("text-[8rem] leading-[0.85] text-center relative z-10 whitespace-pre-line drop-shadow-lg", titleFontClass, currentTheme.accent)} style={{ WebkitTextStroke: '16px white', paintOrder: 'stroke fill' } as React.CSSProperties}>{title}</h1>
                    <h1 className={cn("absolute top-2 left-0 w-full text-[8rem] leading-[0.85] text-center text-black/10 z-0 whitespace-pre-line", titleFontClass)} style={{ WebkitTextStroke: '16px rgba(0,0,0,0.1)' } as React.CSSProperties}>{title}</h1>
                </div>
             </div>

             <div className="flex-1 w-full flex items-center justify-center relative -mt-8">
                 <div className="absolute w-[600px] h-[600px] rounded-full blur-[70px] bg-white/50"></div>
                 <div className={cn("relative w-[460px] h-[880px] rounded-[64px] overflow-hidden z-20 border-[18px] bg-white shadow-[0_30px_60px_-15px_rgba(50,50,93,0.3),_0_18px_36px_-18px_rgba(0,0,0,0.4)]", currentTheme.borderColor)} style={phoneTransformStyle}>
                    <div className={cn("w-full h-full overflow-hidden relative rounded-[46px] border-[4px] border-slate-100", "bg-slate-50")}>
                        {image ? <img src={image} className="w-full h-full" style={{ objectFit: imageFit, ...phoneContentStyle }} /> : <div className="w-full h-full flex flex-col items-center justify-center bg-blue-50 p-6"><ImageIcon size={64} className="text-blue-300"/></div>}
                        <div className="absolute top-0 right-0 w-[250px] h-[900px] bg-gradient-to-l from-white/20 to-transparent skew-x-[-20deg] pointer-events-none"></div>
                    </div>
                 </div>
                 <div className="absolute top-[20%] right-[10%] transform rotate-[15deg] z-30"><div className={cn("p-3 rounded-2xl border-[4px] border-white shadow-sm", "bg-[#FFEB3B]")}><Star size={48} className="text-[#FBC02D]" fill="currentColor" /></div></div>
             </div>

             <div className="w-full max-w-[800px] bg-white rounded-[40px] p-4 border-[6px] border-white/50 shadow-[0_15px_40px_rgba(0,0,0,0.1)] relative mt-0 flex flex-col items-center mb-4">
                {ctaText && <div className={cn("absolute -top-5 bg-[#66BB6A] text-white px-6 py-2 rounded-2xl text-xl border-[4px] border-white shadow-sm transform rotate-[-2deg]", titleFontClass)}>{ctaText}</div>}
                <div className="flex items-center justify-center gap-6 mt-4 w-full px-2">
                    <img src="/appstore.png" className="w-16 h-16 object-contain" /><div className="text-center"><h3 className={cn("text-3xl text-slate-800 whitespace-nowrap", bodyFontClass)}>{storeTitle}</h3><p className="text-slate-500">{storeSubtitle}</p></div><img src="/google.png" className="w-16 h-16 object-contain" />
                </div>
                <div className="flex items-center gap-2 mt-3 text-slate-400 text-sm animate-pulse"><ArrowDown size={18} /><span>LÄ°NK PROFÄ°LDE (BIO)</span><ArrowDown size={18} /></div>
             </div>
          </div>
        )}

      </div>
    </div>
  );
}